import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Button, Table, Col, Form, Input, Select, Spin } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getCodeStudent, getBonusType, getBonusTypeChild } from 'services/AddReward';
import DatePickerString from 'components/common/input/DatePickerString';

import {
  getDisciplineBehavior,
  getDiscipline,
  // getDisciplineBehaviorId,
  getDisciplineById,
  addDiscipline,
} from 'services/AddDiscipline';

const { Option } = Select;
const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
    listDataDiscipline: [],
    isPending: false,
    bonusType: [],
    bonusTypeChild: [],
    dataDisciplinebehavior: [],
    dataDiscipline: [],
  };

  componentDidMount = () => {
    const { match } = this.props;
    this.getData();
    getBonusType()
      .then(res => {
        this.setState({
          bonusType: res.data.items,
        });
      })
      .catch(error => {
        console.log(error);
      });
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getDisciplineById(match.params.id);
    }
  };

  getData = async () => {
    const dataDisciplinebehavior = await getDisciplineBehavior();
    const dataDiscipline = await getDiscipline();
    this.setState({
      dataDisciplinebehavior: dataDisciplinebehavior.data.items,
      dataDiscipline: dataDiscipline.data.items,
    });
  };

  getDisciplineById = async id => {
    const { form } = this.props;
    const dataMap = await getDisciplineById(id);
    const initialValue = handleInitialValue(dataMap.data);
    // dataMap.data.students.forEach(student => {
    //   this.chooseBonusType(student.parentBonusTypeId);
    // });
    this.setState({
      initialValue,
      listDataDiscipline: dataMap.data.students,
      dataGetId: dataMap.data,
    });
    form.setFieldsValue(initialValue);
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { listDataDiscipline, teacherId, dataGetId } = this.state;
        const data = [];
        if (teacherId) {
          const student = [];
          // eslint-disable-next-line array-callback-return
          listDataDiscipline.map(item => {
            const dataItem = {
              studentId: item.id,
              classId: item.classId,
              bonusTypeId: item.bonusTypeId,
              disciplineId: item.disciplineId,
              disciplineBehaviorId: item.disciplineBehaviorId,
              description: item.description,
            };
            student.push(dataItem);
          });
          const dataAdd = {
            approvalDate: values.approvalDate,
            approvalNumber: values.approvalNumber,
            description: values.description,
            id: dataGetId.id,
            semesterId: values.semesterId,
            yearEduId: values.yearEduId,
            students: student,
          };

          addDiscipline(dataAdd)
            .then(() => {
              // console.log('res', res);
              // message.success('');
              this.props.history.goBack();
            })
            .catch(error => console.log('error', error));
        } else {
          listDataDiscipline.forEach(item => {
            const { id, classId, disciplineId, disciplineBehaviorId, description } = item;
            const dataAddnew = {
              studentId: id,
              classId,
              disciplineId,
              disciplineBehaviorId,
              description,
            };

            data.push(dataAddnew);
          });

          const addDataNew = {
            ...values,
            students: data,
          };
          addDiscipline(addDataNew)
            .then(() => {
              // console.log('res', res);
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('error', error);
            });
        }
      }
    });
  };

  addDataAddReward = () => {
    const { listDataDiscipline } = this.state;
    const item = {
      id: '',
      classId: '',
      disciplineId: '',
      disciplineBehaviorId: '',
      description: '',
    };
    listDataDiscipline.push(item);
    this.setState({
      listDataDiscipline,
    });
  };

  deleteData = index => {
    const { dataAddReward } = this.state;
    dataAddReward.splice(index, 1);
    this.setState({
      dataAddReward,
    });
  };

  changeInputCode = (e, record, number) => {
    const { listDataDiscipline } = this.state;
    this.setState({
      isPending: true,
    });
    getCodeStudent(e.target.value)
      .then(res => {
        // eslint-disable-next-line array-callback-return
        listDataDiscipline.map((item, index) => {
          if (index === number) {
            item.id = res.data.id;
            item.fullname = res.data.firstname + res.data.lastname;
            item.dob = res.data.dob;
            item.className = res.data.className;
            // item.classId = res.data.classId;
          }
        });
        this.setState({
          listDataDiscipline,
          isPending: false,
        });
      })
      .catch(error => {
        console.log('error', error);
        this.setState({
          isPending: false,
        });
      });
  };

  changeBonusTypeChild = (e, number) => {
    const { dataAddReward } = this.state;
    // eslint-disable-next-line array-callback-return
    dataAddReward.map((item, index) => {
      if (index === number) {
        item.bonusTypeId = e;
      }
    });
    this.setState({
      dataAddReward,
    });
  };

  chooseBonusType = value => {
    getBonusTypeChild(value)
      .then(res => {
        this.setState({
          bonusTypeChild: {
            ...this.state.bonusTypeChild,
            [value]: res.data.items,
          },
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  changeDataClassId = (value, number) => {
    const { listDataDiscipline } = this.state;
    // eslint-disable-next-line array-callback-return
    listDataDiscipline.map((item, index) => {
      if (index === number) {
        item.classId = value;
      }
    });
    this.setState({
      listDataDiscipline,
    });
  };

  changeDataDisciplineId = (value, number) => {
    const { listDataDiscipline } = this.state;
    // eslint-disable-next-line array-callback-return
    listDataDiscipline.map((item, index) => {
      if (index === number) {
        item.disciplineId = value;
      }
    });
    this.setState({
      listDataDiscipline,
    });
  };

  changeDataDisciplineBehaviorId = (value, number) => {
    const { listDataDiscipline } = this.state;
    // eslint-disable-next-line array-callback-return
    listDataDiscipline.map((item, index) => {
      if (index === number) {
        item.disciplineBehaviorId = value;
      }
    });
    this.setState({
      listDataDiscipline,
    });
  };

  changeDescription = (e, number) => {
    const { listDataDiscipline } = this.state;
    // eslint-disable-next-line array-callback-return
    listDataDiscipline.map((item, index) => {
      if (index === number) {
        item.description = e.target.value;
      }
    });
    this.setState({
      listDataDiscipline,
    });
  };

  render() {
    const {
      initialValue,
      dataDisciplinebehavior,
      dataDiscipline,
      isPending,
      bonusType,
      listDataDiscipline,
    } = this.state;
    const { getFieldDecorator } = this.props.form;
    const { teacherId } = this.state;
    const columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        // eslint-disable-next-line no-unused-vars
        render: (text, record, index) => {
          const { code } = record;
          if (teacherId && code) {
            return <Input value={code} onBlur={e => this.changeInputCode(e, record, index)} />;
          }
          return <Input onBlur={e => this.changeInputCode(e, record, index)} />;
        },
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Cấp kỷ luật',
        width: '20%',
        render: (text, record, index) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.classId}
            onChange={e => this.changeDataClassId(e, index)}
          >
            {bonusType.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Hành vi kỷ luật',
        width: '20%',
        render: (text, record, index) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.disciplineBehaviorId}
            onChange={e => this.changeDataDisciplineBehaviorId(e, index)}
          >
            {dataDisciplinebehavior.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Loại kỷ luật',
        width: '20%',
        render: (text, record, index) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.disciplineId}
            onChange={e => this.changeDataDisciplineId(e, index)}
          >
            {dataDiscipline.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Nội dung xử lý',
        width: '10%',
        render: (text, record, index) => (
          <Input
            defaultValue={record.description}
            onChange={e => this.changeDescription(e, index)}
          />
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'rewardType',
        render: (text, record, index) => (
          <Button
            className="btn-delete"
            icon="delete"
            size="small"
            onClick={() => this.deleteData(index)}
          />
        ),
      },
    ];
    return (
      <Card title={teacherId ? 'Cập nhật nhập kỷ luật' : 'Thêm nhập kỷ luật'}>
        <Row>
          <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Số QĐ">
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Ngày QD">
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chọn ngày" />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Nội dung xử lý">
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <YearSemesterInput form={this.props.form} />
            <Col xs={24} sm={12} md={6}>
              <Form.Item label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel href="#reward/add" />
              </Form.Item>
            </Col>
          </Form>
        </Row>
        <Row>
          <BtnAddNew onClick={this.addDataAddReward} />
        </Row>
        <Row>
          <Col span={24}>
            <Spin spinning={isPending}>
              <Table onRow={this.rowTable} columns={columns} dataSource={listDataDiscipline} />
            </Spin>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



